/* ============================================================ //
// @ layout container
// ============================================================ */

.container--full {
  width: 100%;
  min-width: 1200px;
  @include max-media($screen-sm) {
    min-width: 100%;
    overflow-x: hidden;
  }
}

/* @ メインコンテナー
// ------------------------------ */

.top-main-container{
  //margin-top: 600px;
  margin-top:100vh;
  background: #fff;
  position: relative;
  z-index: 1;
  @include max-media($screen-sm){
    margin-top:45.8333333%;
    position: relative;
    top:$header-hight;
  }
}


/* @ 汎用コンテナ
// ------------------------------ */


.container__inner {
  margin: 0 auto;
  padding: 0 130px;
  width: 100%;
  min-width: 1200px;
  @include max-media($screen-sm){
    padding:0 4%;
    min-width:100%;
  }
}

